<template>
    <div class="box">
        <navbar></navbar>
        <div class="content">
            <div class="center-box warp-1200 ov">
                <centerbar></centerbar>
                <div class="right fl">
                    <p class="balance"><span>我的邀请码</span></p>
                    <div class="ba-box ov">
                        <p class="fl balance-b" style="margin-top: 24px;"><span>我的邀请码</span> <i>{{inviteData.invite_code}}</i></p>
                        <div class="fr tx-btn ov">
                            <img  v-show="false" src="" class="fl" alt="" style="width: 70px;height: 70px;background: #333;display: inline-block;">
                            <p class="fl copy"><img src="@/static/images/tixian.png" width="25" height="22" alt=""><span @click="copy(inviteData.invite_code)">复制</span></p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import { Navbar, Centerbar } from '@/components'
    import {invite} from "../../api/center";
    import Clipboard from 'clipboard'
    export default {
        name: "Invite",
        components: {
            Navbar,
            Centerbar
        },
        data(){
            return {
                codeState:false,
                inviteData:{
                    invite_code:'',

                }
            }
        },
        mounted()
        {
            this.getInvite()
        },
        methods: {

            getInvite()
            {
                invite().then(res => {
                   this.inviteData = res

                })
            },
            // 复制邀请码
            copy(copyText)
            {

                var clipboard = new Clipboard('.copy', {
                    text: function() {
                        return copyText;
                    }
                });
                clipboard.on('success', e => {

                    this.$message({
                        message: '复制成功',
                        type: 'success'
                    });
                    // 释放内存
                    clipboard.destroy()
                })
                clipboard.on('error', e => {
                    this.$message({
                        message: '复制失败, 当前浏览器不支持',
                        type: 'error'
                    });
                    // 释放内存
                    clipboard.destroy()
                })

            },
        }
    }
</script>

<style scoped>
     @import "../../static/css/invite.css";
     .box {height: 100%;background: #F2F4F9;}
</style>



